import React,{ useState } from 'react';
import About from './component/About';
import Work from './component/Work';
import './App.css';

function App() {
  let conArr = {
    'About':<About />,
    'Work':<Work />
  };
  let [cpnt,setCpnt]=useState(conArr['About']);

  function nav(e){
    let menuTxt = e.target.textContent;
    setCpnt(conArr[menuTxt]);
    console.log(menuTxt);  
  }
  

  return(
    <>
    <header>
      <nav onClick={nav}>
        <a>About</a>
        <a>Work</a>
      </nav>
    </header>
    <main>
      {cpnt}
    </main>
    </>
  );
}

export default App;
